.progress-bar {
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  z-index: 1000;
}
@-webkit-keyframes loading {
  0% {
    opacity: 1;
    width: 0%;
  }
  90% {
    width: 80%;
  }
  100% {
    width: 90%;
  }
}
@keyframes loading {
  0% {
    opacity: 1;
    width: 0%;
  }
  90% {
    width: 80%;
  }
  100% {
    width: 90%;
  }
}

@-webkit-keyframes complete {
  0% {
    width: 90%;
  }
  90% {
    width: 100%;
    opacity: 1;
  }
  100% {
    width: 0%;
    opacity: 0;
  }
}

@keyframes complete {
  0% {
    width: 90%;
  }
  90% {
    width: 100%;
    opacity: 1;
  }
  100% {
    width: 0%;
    opacity: 0;
  }
}

@-webkit-keyframes color-transition {
  from {
    background-position: 100%;
  }
  to {
    background-position: 0%;
  }
}

@keyframes color-transition {
  from {
    background-position: 100%;
  }
  to {
    background-position: 0%;
  }
}
